import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

const USER_NAME=null;
const HAS_LOGGED_IN="0";

@Injectable()
export class GlobalVars{
	//HAS_LOGGED_IN : any;
	//USER_NAME : any;
	DEVICE_ID : any; 
  constructor(public storage: Storage) {
	  //this.DEVICE_ID = "";
	  //this.HAS_LOGGED_IN="0";
	  this.DEVICE_ID="";
  }
/*  initialize(){	
    this.storage.set(HAS_LOGGED_IN, null);
    this.storage.set(USER_NAME, null);
}*/
  setUsername(username) {
	let set="1";
	this.storage.set(HAS_LOGGED_IN, set);
    this.storage.set(USER_NAME, username);
  }
  getUsername() {
    return this.storage.get(USER_NAME);
  }
  clearStorage() {
	//let clearusername="";
    return this.storage.clear();
  }
  
}


