import { Component } from '@angular/core';
//import { NavController, Platform, LoadingController } from 'ionic-angular';

import { NavController, NavParams } from 'ionic-angular';

import { SettingsPage } from '../settings/settings';
import { AboutPage } from '../about/about';
import { ProfilePage } from '../profile/profile';
import { ConnectServer } from '../../providers/connectserver';

import { LoaderView } from '../../providers/loaderview';

/**
 * Generated class for the TripsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  templateUrl: 'trips.html',
})
export class TripsPage {

	tab: string = "Attractions";

	attractions_array : any;
	hotels_array : any;
	restaurants_array : any;

	temp_attractions_array : any;
	temp_hotels_array : any;
	temp_restaurants_array : any;
	
   	attractions_array_other : any;
   	hotels_array_other : any;
  	restaurants_array_other : any;

	temp_attractions_array_other : any;
   	temp_hotels_array_other : any;
  	temp_restaurants_array_other : any;

	attractions_array_loaded : number;
	hotels_array_loaded : number;
	restaurants_array_loaded : number;

	userData: any;
	message :any;
	
  constructor(public navCtrl: NavController, 
  		public navParams: NavParams,
		private connectServer: ConnectServer,
      	private loaderView : LoaderView){
  
		this.attractions_array = [];
      	this.hotels_array = [];
      	this.restaurants_array = [];

      	this.temp_attractions_array = [];
      	this.temp_hotels_array = [];
      	this.temp_restaurants_array = [];

		this.attractions_array_other = [];
      	this.hotels_array_other = [];
      	this.restaurants_array_other = [];
		
      	this.temp_attractions_array_other = [];
      	this.temp_hotels_array_other = [];
      	this.temp_restaurants_array_other = [];
		
		this.attractions_array_loaded  = 0;
		this.hotels_array_loaded = 0;
		this.restaurants_array_loaded = 0;
		this.userData = {Username : "",ageRange: "",gender: "",numHotelsReviews: "",numRestReviews: "",numAttractReviews: "",num1irstToReview: "",numRatings: "",numPhotos: "",num1orumPosts: "",numArticles: "",numCitiesBeen: "",totalPoints: "",contribLevel: "",numHelp1ulVotes: "",reviewerBadge: "",travelStyle: "",taObjectCity:"",type:""};
		
		this.message = "";
  }

  ionViewDidLoad() {
      this.message = this.navParams.get("userData")['message'];
	  this.userData.Username = this.navParams.get("userData").Username;
	  this.userData.ageRange = this.navParams.get("userData").ageRange;
	  this.userData.gender = this.navParams.get("userData").gender;
	  this.userData.numHotelsReviews = this.navParams.get("userData").numHotelsReviews;
	  this.userData.numRestReviews = this.navParams.get("userData").numRestReviews;
	  this.userData.numAttractReviews = this.navParams.get("userData").numAttractReviews;
	  this.userData.num1irstToReview = this.navParams.get("userData").num1irstToReview;
	  this.userData.numRatings = this.navParams.get("userData").numRatings;
	  this.userData.numPhotos = this.navParams.get("userData").numPhotos;
	  this.userData.num1orumPosts = this.navParams.get("userData").num1orumPosts;
	  this.userData.numArticles = this.navParams.get("userData").numArticles;
	  this.userData.numCitiesBeen = this.navParams.get("userData").numCitiesBeen;
	  this.userData.totalPoints = this.navParams.get("userData").totalPoints;
	  this.userData.contribLevel = this.navParams.get("userData").contribLevel;
	  this.userData.numHelp1ulVotes = this.navParams.get("userData").numHelp1ulVotes;
	  this.userData.reviewerBadge = this.navParams.get("userData").reviewerBadge;
	  this.userData.travelStyle = this.navParams.get("userData").travelStyle;
	  this.userData.taObjectCity = this.navParams.get("userData").taObjectCity;
	  this.fetchData(0);
	  
	}
  fetchData(tab) { 	
	//alert(tab);	
  	if(tab == 0)
		{
		this.userData.type = "Attractions"
		if(!this.attractions_array_loaded)
			{
  			//console.log('attractions_array_loaded');
		    this.loaderView.showLoader('Fetching nearby Attractions...');
            //console.log(this.userData.Username);
			this.connectServer.getData("Dashboard",this.userData).then(
            response => {
                       this.message = "";
                       this.loaderView.dismissLoader();
						   if(response!=null)
						   {
						   if(response['response'][0]['success'] == "1")
								{
									this.message = response['response'][1]['message'];
									this.attractions_array = response['response'][2]['places'];
									this.temp_attractions_array=this.attractions_array.places;
									this.attractions_array_other = response['response'][3]['places_other'];
									this.temp_attractions_array_other=this.attractions_array_other.places_other;
									this.attractions_array_loaded = 1;								
								}
							else
								{
								this.message = "Could not find Attractions";
								}
							}
						else
							{
							this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
		}
		
	if(tab == 1)
		{
		this.userData.type = "Hotels"
		if(!this.hotels_array_loaded)
			{
  			//console.log('hotels_array_loaded');
		    this.loaderView.showLoader('Fetching nearby Hotels...');
            this.connectServer.getData("Dashboard",this.userData).then(
            response => {
                       this.message = "";
                       this.loaderView.dismissLoader();
						if(response!=null)
							{
						   if(response['response'][0]['success'] == "1")
								{
									this.message = response['response'][1]['message'];
									this.hotels_array = response['response'][2]['places'];
									this.temp_hotels_array=this.hotels_array.places;
									this.hotels_array_other = response['response'][3]['places_other'];
									this.temp_hotels_array_other=this.hotels_array_other.places_other;
									//console.log(this.temp_hotels_array);
									this.hotels_array_loaded = 1;								
								}
							else
								{
								this.message = "Could not find nearby Hotels";
								}
							}
						else
							{
							this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
		}
	if(tab == 2)
		{
		this.userData.type = "Restaurants"
		if(!this.restaurants_array_loaded)
			{
  			//console.log('restaurants_array_loaded');
		    this.loaderView.showLoader('Fetching nearby Restaurants...');
            this.connectServer.getData("Dashboard",this.userData).then(
            response => {
                       this.message = "";
                       this.loaderView.dismissLoader();
						if(response!=null)
							{
						   if(response['response'][0]['success'] == "1")
								{
									this.message = response['response'][1]['message'];
									this.restaurants_array = response['response'][2]['places'];
									this.temp_restaurants_array=this.restaurants_array.places;
									this.restaurants_array_other = response['response'][3]['places_other'];
									this.temp_restaurants_array_other=this.restaurants_array_other.places_other;
									//console.log(this.temp_restaurants_array);
									this.restaurants_array_loaded = 1;								
								}
							else
								{
								this.message = "Could not find Restaurants";
								}
							}
						else{
							this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
		}
		
	}
}



