import { Injectable } from '@angular/core';
//import { Http, RequestOptions, Headers } from '@angular/http';
import { Http } from '@angular/http';
import { GlobalVars } from '../providers/globalvars';
import 'rxjs/add/operator/map';

@Injectable()
export class ConnectServer {
  
  serverURL: string;

  constructor(
    public http: Http,
    private globalVars : GlobalVars ) {
	//this.serverURL = "http://192.168.29.62:3134/";
	//this.serverURL = "http://10.0.2.121:3134/";
	//this.serverURL ="http://172.20.10.4:3134/";
	this.serverURL ="http://13.126.179.146/";
	}

  login(objData) {
      return new Promise(resolve => {
        
        objData['device'] = this.globalVars.DEVICE_ID;
        var sURL = this.serverURL + "Login" + this.generateQueryString(objData);
        //var sURL="http://192.168.29.62:3134/Login?Username=viken&Password=parikh";
        this.http.get(sURL)
          .map(res => res.json())
          .subscribe(data => {
				resolve(data);
				}, error => {
				alert("We are unable to connect to our servers. Please try after some time.");
				//var objResponse = [];
				//objResponse['success'] = -1;
				resolve(null);
				//console.log(JSON.stringify(error.json()));
			});
		});
	}
	
	getData(fileURL, objData) {
      return new Promise(resolve => {
		if(objData == null)
        {
          objData = [];
        }
        var sURL = this.serverURL + fileURL + this.generateQueryString(objData);
        this.http.get(sURL)
          .map(res => res.json())
          .subscribe(data => {
            console.log(data);
            resolve(data);
          }, error => {
            alert("We are unable to connect to our servers. Please try after some time.");
            //var objResponse = [];
            //objResponse['success'] = -1;
            resolve(null);
            //console.log(JSON.stringify(error.json()));
          });
    });
  }

	
    
  generateQueryString(objData) {

    var sQueryString = "";
    if(Object.keys(objData).length > 0)
    {
      for(var i = 0; i < Object.keys(objData).length; i++)
      {
        var sData = (Object.keys(objData)[i] + '=' + objData[Object.keys(objData)[i]]);
        sQueryString += sData + '&';
      }
      sQueryString += "v=0.9.170504";

      if(sQueryString.length > 0)
      {
        sQueryString = "?" + sQueryString;
      }
    }

    return sQueryString;
  }

}
 


