import { Component } from '@angular/core';
import { NavController, Platform, LoadingController, ToastController , NavParams} from 'ionic-angular';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { Camera, CameraOptions } from '@ionic-native/camera';

import { TripsPage } from '../trips/trips';
import { RegisterPage } from '../register/register';
import { CommentPage } from '../comment/comment';
import { SettingsPage } from '../settings/settings';
import { AboutPage } from '../about/about';
import { ProfilePage } from '../profile/profile';
import { ConnectServer } from '../../providers/connectserver';
import { LoaderView } from '../../providers/loaderview';

import { GlobalVars } from '../../providers/globalvars';

/*
  Generated class for the DashboardPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@Component({
  templateUrl: 'dashboard.html',
})
export class DashboardPage {
	message : any;
	message1 : any;
	message2 : any;
	
	userData :any;
	imageURI:any;
	imageFiletoDisplay:any;
	cities:any;
	Imageuploadurl:any;
	image:string;
	uploadImage:any;
  constructor (
        public navCtrl: NavController, 
  		public navParams: NavParams,
		private connectServer: ConnectServer,
		private loaderView : LoaderView,
		private transfer: FileTransfer,
		private camera: Camera,
		public loadingCtrl: LoadingController,
		private globalVars: GlobalVars,
		public toastCtrl: ToastController) {
	    this.userData = {Username : "", ageRange: "",gender: "",numHotelsReviews: "",numRestReviews: "",numAttractReviews: "",num1irstToReview: "",numRatings: "",numPhotos: "",num1orumPosts: "",numArticles: "",numCitiesBeen: "",totalPoints: "",contribLevel: "",numHelp1ulVotes: "",reviewerBadge: "",travelStyle: "",taObjectCity:"",type:"",message:"",filename:""};
	    this.imageFiletoDisplay ="";
		this.message = "";
		this.message1 = "";
		this.message2 = "";
		this.cities=[];
		this.Imageuploadurl="";
		this.uploadImage="";
		};
		
  ionViewDidLoad() {
      this.message = "";
	  this.message1 = "";
	  this.message2 = "";
	  this.Imageuploadurl="";
	  this.uploadImage="";
	  this.message = this.navParams.get("userData")['response'][1]['message'];
	  /*
	  if(this.globalVars.getUsername()==null)
		{
		  */
		  this.userData.Username = this.navParams.get("userData")['response'][2]['username'];
		  this.userData.ageRange = this.navParams.get("userData")['response'][3]['ageRange'];
		  this.userData.gender = this.navParams.get("userData")['response'][4]['gender'];
		  this.userData.numHotelsReviews = this.navParams.get("userData")['response'][5]['numHotelsReviews'];
		  this.userData.numRestReviews = this.navParams.get("userData")['response'][6]['numRestReviews'];
		  this.userData.numAttractReviews = this.navParams.get("userData")['response'][7]['numAttractReviews'];
		  this.userData.num1irstToReview = this.navParams.get("userData")['response'][8]['num1irstToReview'];
		  this.userData.numRatings = this.navParams.get("userData")['response'][9]['numRatings'];
		  this.userData.numPhotos = this.navParams.get("userData")['response'][10]['numPhotos'];
		  this.userData.num1orumPosts = this.navParams.get("userData")['response'][11]['num1orumPosts'];
		  this.userData.numArticles = this.navParams.get("userData")['response'][12]['numArticles'];
		  this.userData.numCitiesBeen = this.navParams.get("userData")['response'][13]['numCitiesBeen'];
		  this.userData.totalPoints = this.navParams.get("userData")['response'][14]['totalPoints'];
		  this.userData.contribLevel = this.navParams.get("userData")['response'][15]['contribLevel'];
		  this.userData.numHelp1ulVotes = this.navParams.get("userData")['response'][16]['numHelp1ulVotes'];
		  this.userData.reviewerBadge = this.navParams.get("userData")['response'][17]['reviewerBadge'];
		  this.userData.travelStyle = this.navParams.get("userData")['response'][18]['travelStyle'];
		  this.cities =this.navParams.get("userData")['response'][19]['cities']['taObjectCity'];
		/*  this.globalVars.setUsername(this.userData.Username);
		}
	  else{
		  this.userData.Username=this.globalVars.getUsername()
		  }*/
	  this.userData.taObjectCity="Mumbai (Bombay)";
	  }
	  
	findplaces(){
		this.message1 = "";
		this.message2 = "";
        if(this.userData.taObjectCity.length == 0)
			{
            this.message1 = "Please enter the Cityname";
			this.message2 = "";
			}
        else
			{
            this.loaderView.showLoader('Finding places...');
            this.message1 = "Finding Places. Please Wait ...";  
			this.message2 = "";
            this.loaderView.dismissLoader();
			this.navCtrl.push(TripsPage, {userData : this.userData});
			}
	}
	
	Profile()
		{
		    this.loaderView.showLoader('Loading Profile...');
            this.loaderView.dismissLoader();
			this.navCtrl.push(ProfilePage, {userData : this.userData});
		}
	
	Comment()
		{
		if(this.userData.taObjectCity.length == 0)
			{
            this.message1 = "";
			this.message2 = "Please enter the Cityname";
			}
		else{
			this.loaderView.showLoader('Loading Add Review Page...');
            this.loaderView.dismissLoader();
			this.navCtrl.push(CommentPage, {userData : this.userData});
			}
		}
	
	addcity(){
		this.message2 = "";
		this.message1 = "";
		    //console.log(this.userData.taObjectCity);
		if(this.userData.taObjectCity.length == 0)
			{
            this.message1 = "";
			this.message2 = "Please enter the Cityname";
			}
		else{
			this.loaderView.showLoader('Sending your request. Please wait');
			this.connectServer.getData("Addcities",this.userData).then(
            response => {
                       this.message2 = "";
					   this.message1 = "";
                       this.loaderView.dismissLoader();
					   if(response!=null){
						   if(response['response'][0]['success'] == "1")
								{
									this.message2 = response['response'][1]['message'];
									this.message1 = "";
								}
							else
								{
								this.message2 = "Could not Add City";
								this.message1 = "";
								}
							}
						else{
							this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
	}
	  pictureFromCamera() {
			const options: CameraOptions = {
			  quality: 100,
			  destinationType: this.camera.DestinationType.DATA_URL,
			  encodingType: this.camera.EncodingType.JPEG,
			  mediaType: this.camera.MediaType.PICTURE,
			  correctOrientation: true,
			  saveToPhotoAlbum: true
			}

			this.capturePhoto(options);
			}

		pictureFromGallery() {
		//Added PictureSourceType.PHOTOLIBRARY to access from gallery.
		
		if(this.userData.taObjectCity.length == 0)
			{
            this.message1 = "Please enter the Cityname";
			this.message2 = "";
			}
		else{
			const options: CameraOptions = {
			  quality: 100,
			  destinationType: this.camera.DestinationType.DATA_URL,
			  encodingType: this.camera.EncodingType.JPEG,
			  sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
			  mediaType: this.camera.MediaType.PICTURE,
			  correctOrientation: true,
			  saveToPhotoAlbum: true
			}
			this.capturePhoto(options);	
			}		
		}
  
		async capturePhoto(options: CameraOptions) {
			try {
				  //Result is a base64 image but can be changed to use a filepath.
				  const result = await this.camera.getPicture(options)
				  //Append result to image to display in view
				  this.image = `data:image/jpeg;base64,${result}`;
				  
				  //this.Imageuploadurl = "http://192.168.29.62:3134/upload"+this.connectServer.generateQueryString(this.userData);
				  //this.Imageuploadurl = "http://10.0.2.121:3134/upload"+this.connectServer.generateQueryString(this.userData);
				  this.Imageuploadurl ="http://13.126.179.146/upload";
				  let loader = this.loadingCtrl.create({
					content: "Uploading..."
				  });
					const fileTransfer: FileTransferObject = this.transfer.create();
					this.uploadImage = this.uploadImageFilename();
					this.userData.filename = this.uploadImage;
							  let options1: FileUploadOptions = {
								//Naming the file here
								fileKey: 'ionicfile',
								fileName: this.uploadImage,
								chunkedMode: false,
								mimeType: "image/jpeg",
								headers: {}
							  }
					fileTransfer.upload(this.image,this.Imageuploadurl , options1).then(response => {
						this.message2 = "";
						this.message1 = "";
						if(response!=null)
										{
									   if(response['response'][0]['success'] == "0")
											{
											loader.dismiss();
											this.message2 = response['response'][1]['message'];
											}
										else
											{
											loader.dismiss();
											this.message2 = response['response'][1]['message'];
											this.message1 = "";
											//console.log(response+" Uploaded Successfully");
											//this.imageFiletoDisplay = "http://13.126.179.146/" + this.uploadImage+ ".jpg";
											this.imageFiletoDisplay=this.image;
											this.connectServer.getData("Recognize",this.userData).then(
											response1 => {
													   this.message2 = "";
													   this.message1 = "";
													   if(response1!=null)
															{
														   if(response1['response'][0]['success'] == "1")
																{
																	this.message2 = "";
																	this.message1 = "";
																	let p = response1['response'][2]['place'];
																	this.presentToast("Image uploaded successfully. The place is " + p);
																}
															else
																{
																this.message2 = "Could not Find Place ";
																this.message1 = "";

																}
															}
														else{
															this.message = "Couldn't Connect to the server";
															}
														}
													);
											}
											this.message1 = "";	
										}
						else
										{
										this.message2 = "Could not connect to server";
										this.message1 = "";	
										}
										
									}, (err) => {
														console.log(err);
														loader.dismiss();
														this.presentToast(err);
												});	
				}
				catch (e) 
				{
				console.error(e);
				}
		}
		
	presentToast(msg) {
		let toast = this.toastCtrl.create({
		message: msg,
		duration: 3000,
		position: 'bottom'
		});
		toast.onDidDismiss(() => {
		console.log('Dismissed toast');
		});
		toast.present();
		}
		
		
	uploadImageFilename()
		{
		let myDate = new Date().toISOString();
		return this.userData.Username+myDate;
		}
		
		
}



