import { Component } from '@angular/core';
import { NavController, Platform, LoadingController, ToastController, NavParams } from 'ionic-angular';
//import { NavController } from 'ionic-angular';
//import { Platform } from 'ionic-angular';
//import { InAppBrowser, SpinnerDialog } from 'ionic-native';
import { ConnectServer } from '../../providers/connectserver';
import { LoaderView } from '../../providers/loaderview';
import { DashboardPage } from '../dashboard/dashboard';
import { LoginPage } from '../login/login';


import { GlobalVars } from '../../providers/globalvars';


/**
 * Generated class for the RegisterPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  	userData: any;
    showLogin : any;
    message : any;
	travelStyles :any;
  	constructor(
        private navCtrl: NavController,
        private globalVars: GlobalVars,
        private connectServer: ConnectServer,
        private platform : Platform,
        private loaderView : LoaderView,
		public toastCtrl: ToastController) {
		this.userData = {Username : "", Password : "", ageRange: "0",gender: "0",numHotelsReviews: "0",numRestReviews: "0",numAttractReviews: "0",num1irstToReview: "0",numRatings: "0",numPhotos: "0",num1orumPosts: "0",numArticles: "0",numCitiesBeen: "0",totalPoints: "0",contribLevel: "0",numHelp1ulVotes: "0",reviewerBadge: "0",travelStyle: "",taObjectCity:"",type:"",message:""};
        this.travelStyles=[];
		this.showLogin = false;   
        this.message = "";
  	}
	
  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPage');
  
  }
  
  Signin()
	{
		this.loaderView.showLoader('Loading Login Page...');
        this.loaderView.dismissLoader();
		this.navCtrl.setRoot(LoginPage);
	}
    Register() 
		{
		this.message = "";
		if(this.userData.Username.length == 0 || this.userData.Password.length == 0)
			{
            this.message = "Please enter Both Username and Password";
			}
        else
			{
			if(this.travelStyles.length >0){
				this.userData.travelStyle=this.travelStyles.join();}
				//console.log("Hey"+this.travelStyles.join());}
			else{
				this.userData.travelStyle="Default";
				}
			//console.log(this.userData);
            this.loaderView.showLoader('Verifying Details ...');
            this.message = "Verifying Details. Please Wait ...";  
            this.connectServer.getData("Register",this.userData).then(
            response => { 
                       this.message = "";
                       this.loaderView.dismissLoader();
					   console.log(response);
					   if(response!=null)
							{	
							if(response['response'][0]['success'] == "0")
								{
									this.message = response['response'][1]['message'];          
								}
							else if(response['response'][0]['success'] == "1")
								{
									this.presentToast(response['response'][1]['message']);
									this.navCtrl.setRoot(LoginPage);
								}
							else
								{
								this.message = "Couldnt Register User!";
								}
							}
						else
							{
								this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
		}
		
		
		presentToast(msg) {
		let toast = this.toastCtrl.create({
		message: msg,
		duration: 3000,
		position: 'bottom'
		});
		toast.onDidDismiss(() => {
		console.log('Dismissed toast');
		});
		toast.present();
		}
	

}



