import { Component } from '@angular/core';
import { NavController, Platform, LoadingController, ToastController , NavParams} from 'ionic-angular';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { Camera, CameraOptions } from '@ionic-native/camera';

import { TripsPage } from '../trips/trips';
import { RegisterPage } from '../register/register';
import { SettingsPage } from '../settings/settings';
import { AboutPage } from '../about/about';
import { ProfilePage } from '../profile/profile';
import { ConnectServer } from '../../providers/connectserver';
import { LoaderView } from '../../providers/loaderview';
import { DashboardPage } from '../dashboard/dashboard';
import { LoginPage } from '../login/login';


import { GlobalVars } from '../../providers/globalvars';

/**
 * Generated class for the CommentPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-comment',
  templateUrl: 'comment.html',
})
export class CommentPage {
   userData:any;
   message:any;
   reviewData :any;
  constructor(public navCtrl: NavController, 
     		  public navParams: NavParams,
			  private connectServer: ConnectServer,
			  private loaderView : LoaderView,
			  public toastCtrl: ToastController) {
			  this.userData = {Username : "", ageRange: "",gender: "",numHotelsReviews: "",numRestReviews: "",numAttractReviews: "",num1irstToReview: "",numRatings: "",numPhotos: "",num1orumPosts: "",numArticles: "",numCitiesBeen: "",totalPoints: "",contribLevel: "",numHelp1ulVotes: "",reviewerBadge: "",travelStyle: "",taObjectCity:"",type:"",message:"",filename:""};
			  this.message = "";	
			  this.reviewData = {Username : "", type : "", taObjectCity : "", taObject : "", rating : "", title : "", text : "",helpfulness : "0", total_points : "100" , taObjectUrl : "Default"};
			  this.message ="";
			  }

  ionViewDidLoad() {
    //console.log('ionViewDidLoad CommentPage');
		  this.reviewData.Username = this.navParams.get("userData").Username;
		  this.reviewData.taObjectCity = this.navParams.get("userData").taObjectCity;		  
  }
    
  Comment(){
  		this.message = "";
		if(this.reviewData.Username.length == 0 )
			{
            this.message = "You are not logged in. Please Register or Login to post a review";
			}
		else if(this.reviewData.type.length == 0 )
			{
            this.message = "Please select the type of place";
			}
		else if(this.reviewData.taObjectCity.length == 0 )
			{
            this.message = "You are not logged in. Please Register or Login to post a review";
			}
		else if(this.reviewData.taObject.length == 0 )
			{
            this.message = "Please Enter the name of place";
			}
		else if(this.reviewData.rating.length == 0 )
			{
            this.message = "Please select a rating for the place you are about to review";
			}
		else if(this.reviewData.title.length == 0 )
			{
            this.message = "Please Enter a title of the place you are about to review";
			}
		else if(this.reviewData.text.length == 0 )
			{
            this.message = "Please Enter a description of the place you are about to review ";
			}
        else
			{
			console.log(this.reviewData);
            this.loaderView.showLoader('Verifying Review ...');
            this.message = "Posting Review. Please Wait ...";  
            this.connectServer.getData("Comment",this.reviewData).then(
            response => { 
                       this.message = "";
                       this.loaderView.dismissLoader();
					   console.log("Yes"+response);
					   if(response!=null)
							{	
							if(response['response'][0]['success'] == "0")
								{
									this.message = response['response'][1]['message'];          
								}
							else if(response['response'][0]['success'] == "1")
								{
									this.presentToast(response['response'][1]['message']);
									this.navCtrl.setRoot(LoginPage,);
								}
							else
								{
								this.message = "Couldnt Post Review! Try Again.";
								}
							}
						else
							{
								this.message = "Couldnt Connect to the server";
							}
						}
				);
			}
		}
		
	presentToast(msg) {
		let toast = this.toastCtrl.create({
		message: msg,
		duration: 3000,
		position: 'bottom'
		});
		toast.onDidDismiss(() => {
		console.log('Dismissed toast');
		});
		toast.present();
		}
}



